<template>
  <div id="chart">
    <pre id="log">UVI: {{uvData}}</pre>
    <apexchart type="line" height="350" ref="chart" :options="chartOptions" :series="series"></apexchart>
</div>
</template>

<script>
  import VueApexCharts from 'vue-apexcharts'

  let data = [];
  let lastDate = 0;
  let XAXISRANGE = 20
  function getDayWiseTimeSeries(baseval, count) {
  var i = 0;
  while (i < count) {
    var x = baseval;
    var y = 0;
    data.push({
      x,
      y
    });
    lastDate = baseval;
    baseval += 1;
    i++;
  }
}

getDayWiseTimeSeries(0, 1);

function getNewSeries(baseval, uv) {
  var newDate = baseval + 1;
  lastDate = newDate;
  data.push({
    x: newDate,
    y: uv
  });
}

function resetData() {
  data = data.slice(data.length - 10, data.length);
}
	export default {
		props: ['uvData'],
    components: {
      'apexchart': VueApexCharts,
    },
		data() {
      return{
        uvD: null,
        series: [{
          data: data.slice()
        }],
        chartOptions: {
          chart: {
            id: 'realtime',
            height: 350,
            type: 'line',
            animations: {
              enabled: true,
              easing: 'linear',
              dynamicAnimation: {
                speed: 500
              }
            },
            toolbar: {
              show: false
            },
            zoom: {
              enabled: false
            }
          },
          fill: {
            type: "gradient",
            gradient: {
              shadeIntensity: 1,
              opacityFrom: 0.7,
              opacityTo: 0.9,
              type: "vertical",
              colorStops: [
              {
                offset: 33.3,
                color: "#A465AA",
                opacity: 1
              },
              {
                offset: 53.3,
                color: "#EE3A12",
                opacity: 1
              },
              {
                offset: 66.6,
                color: "#F68501",
                opacity: 1
              },
              {
                offset: 86.6,
                color: "#FCF302",
                opacity: 1
              },
              {
                offset: 100,
                color: "#41AB24",
                opacity: 1
              },
              ]
            }
          },
          dataLabels: {
            enabled: false
          },
          stroke: {
            curve: 'smooth'
          },
          title: {
            enabled: false
          },
          markers: {
            size: 0
          },
          xaxis: {
            range: XAXISRANGE,
          },
          yaxis: {
            max: 15
          },
          legend: {
            show: false
          },
        },
      }
		},
    watch: {
      uvData() {
        this.uvD = this.uvData;
        console.log(typeof(this.uvD) + this.uvD);
        if (this.uvD != null && this.uvD != '' && this.uvD < 20) {
          // console.log('here')
          getNewSeries(lastDate, this.uvD)
          this.$refs.chart.updateSeries([{
            data: data
          }])
        }
      }
    },
    mounted() {
    }
	}
</script>